  @media (max-width: 768px) {
    .nav-header img{
        display: none;
      }
      .nav-header a {
        margin-left: 0.3rem;
      }
      .nav-footer .bi-person-circle {
        margin-left: 0.6rem;
      }
    
      .nav-footer .bi-pencil-square {
        margin-left: 0.6rem;
      }
      .nav-footer .bi-chevron-down {
        margin-left: 0.6rem;
      }
}
  
  .nav {
    position: fixed;
    display: flex;
    height: 5rem;
    background-color: white;
    border: 0.1rem lightgray solid;
    top: 0;
    width: 100%;
  }
  
  .nav-header {
    flex-grow: 1;
    display: flex;
  }

  .nav-header a {
    display: flex;
    align-items: center;
  }

  .nav img {
    width: 4rem;
    height: 4rem;
    margin-left: 1rem;
  }

  .nav-header h1 {
    margin: 0;
    float: right;
    color: black;
    font-size: 2rem; 
    text-decoration-line: none;
  }
  
  .nav-body {
    padding: 0.3rem;
    display: flex;
    align-items: center;
    flex-grow: 2;
  }
  
  .nav-body .bi-search {
    position: absolute;
    margin: 0rem;
    padding-top: 0.4rem;
    padding-left: 1rem;
  }
  
  .nav-body input {
    border: 0.2rem gray solid;
    border-radius: 50rem;
    font-size: 1rem;
    padding-left: 3rem;
    width: 100%;
    height: 2rem;
  }
  
  .nav-footer {
    display: flex;
    flex-direction: row;
    align-items: center;
    flex-grow: 1;
    justify-content: flex-end;
    padding: 0.5rem;
  }

  .bi-person-circle {
    margin-left: 1.5rem;
  }

  .bi-pencil-square {
    margin-left: 1.5rem;
  }
  .bi-chevron-down {
    margin-left: 1.5rem;
  }
  