.main {
    margin-top: 10rem;
}

.post {
    display: flex;
    flex-direction: column;
    padding: 2rem; /* 상하좌우 여백을 조절 */
    border-radius: 2rem;
    margin: 2rem auto; /* auto를 사용하여 좌우 여백을 자동으로 나눔 */
    max-width: 30rem; /* 필요에 따라 최대 넓이를 지정할 수 있음 */
    background-color: white;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.1); /* 그림자 효과 추가 */
}

.post:hover {
    background-color: lightgrey;
}


/* 미디어 쿼리 추가 (옵션) */
@media (max-width: 768px) {
    .post {
        max-width: 100%; /* 창의 가로 넓이가 768px 이하일 때 최대 넓이를 100%로 변경 */
        margin: 2rem 1rem;
    }
}

.post-header {
    display: flex;
    flex-direction: row; /* 가로 방향으로 정렬 */
    align-items: center; /* 왼쪽으로 정렬 */
}

.post-header img {
    width: 2rem;
    padding-right: 0.5rem;
}

.post-header .name {
    font-size: 0.6rem;
    margin: 0; /* 기본 마진 제거 */
}

.post-header .title {
    margin-top: 0.5rem; /* .name과의 간격을 조절 */
    
}



.post-body {
    display: flex;
    flex-direction: column;
}


.post-body img {
    width: 100%; /* 이미지의 부모 컨테이너에 대해 100%의 너비를 가지도록 설정 */
    height: auto; /* 자동으로 높이를 조정하여 원래 비율을 유지 */
    max-width: 100%; /* 부모 컨테이너를 넘어가지 않도록 최대 너비 설정 */
    float: left;
    margin-right: 1rem;
    border-radius: 3rem;
}


.post-footer {  
    margin-top: 1rem;
}

.post-footer button {
    border-radius: 5rem;
    padding: 0.5rem 0;
    width: 4rem;
    border: none;
    margin-right: 0.5rem;
} 

.post-footer button:hover {
    background-color: darkgrey;
}

